*, html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  width: 100vw;
  display: flex;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #282c34;
  line-height: 1.5;
  font-size: 1.25rem;
  color: white;
  #editor {
    width: 50vw;
    background-color: #ede9da;
    color: black;
    padding: 5rem 2.5rem 0 2.5rem;
    border: none;
    outline: none;
    overflow: auto;
  }
  #preview {
    height: 100%;
    padding: 2.5rem;
  }
  blockquote {
    display: inline-block;
    padding-left: 5px;
    border-left: 3px solid white;
  }
  img {
    width: 300px;
    height: 300px;
  }
}

// Prism (syntax highlighter)

code[class*=language-],
pre[class*=language-] {
  color: #f8f8f2;
  background: 0 0;
  text-shadow: 0 1px rgba(0, 0, 0, .3);
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  font-size: 1em;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none
}

pre[class*=language-] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto;
  border-radius: .3em
}

:not(pre)>code[class*=language-],
pre[class*=language-] {
  background: #272822
}

:not(pre)>code[class*=language-] {
  padding: .1em;
  border-radius: .3em;
  white-space: normal
}

.token.cdata,
.token.comment,
.token.doctype,
.token.prolog {
  color: #8292a2
}

.token.punctuation {
  color: #f8f8f2
}

.token.namespace {
  opacity: .7
}

.token.constant,
.token.deleted,
.token.property,
.token.symbol,
.token.tag {
  color: #f92672
}

.token.boolean,
.token.number {
  color: #ae81ff
}

.token.attr-name,
.token.builtin,
.token.char,
.token.inserted,
.token.selector,
.token.string {
  color: #a6e22e
}

.language-css .token.string,
.style .token.string,
.token.entity,
.token.operator,
.token.url,
.token.variable {
  color: #f8f8f2
}

.token.atrule,
.token.attr-value,
.token.class-name,
.token.function {
  color: #e6db74
}

.token.keyword {
  color: #66d9ef
}

.token.important,
.token.regex {
  color: #fd971f
}

.token.bold,
.token.important {
  font-weight: 700
}

.token.italic {
  font-style: italic
}

.token.entity {
  cursor: help
}